.works {
    &-header {
      &-title {
        color: $default-shade;
        font-family: $serif-primary;
        font-size: 4rem;
        margin: 1rem 0;
        text-align: center;
      }
    }

    &-container {
        display: grid;
        grid-template-columns: repeat(1);
        grid-template-rows: max-content;
        grid-gap: 2rem;
        
        &-item {
            -webkit-animation: slide-up 2s;
            animation: slide-up 2s;
        }

        // &-item:hover img {
        //     -webkit-transform: scale(1.01);
        //     transform: scale(1.01);
        //     transition: .3s cubic-bezier(.2,.8,.2,1);
        // }
    }
    &-message {
        max-width: 25rem;
        margin: 0 auto;
        text-align: center;
    }
}
