.catalogue {
  &-item {
    color: $default-color;
    display: block;
    padding: 2rem 0;

    &:hover .catalogue-line,
    &:focus .catalogue-line {
      width: 100%;
    }

    &:last-child {
      border: 0;
    }
  }

  &-pinned {
    color: $default-tint;
    font-family: $serif-secondary;
    letter-spacing: .5px;
  }

  &-time {
    color: $default-tint;
    font-family: $serif-secondary;
    letter-spacing: .5px;
  }

  &-title {
    color: $default-shade;
    display: block;
    font-family: $serif-primary;
    font-size: 2rem;
    font-weight: 400;
    margin: .5rem 0;
    margin-top: 1rem;
  }

  &-line {
    @include transition(all .3s ease-out);
    border-top: .5px solid $grey-2;
    display: block;
    width: 50%;
  }

  &-thumbnail {
    width: auto;
    margin: auto;
    object-fit: contain;
  }

  &-thumbnail-container {
    background-color: $thumbnail-background-color;
    overflow: hidden;
    max-height: 320px;
  }

  @media (min-width: 600px) {
    &-row {
      display: flex;
      flex-direction: row;
    }

    &-column {
      float: left;
    }
    
    &-column-left {
      width: 30%;
    }

    &-column-right {
      width: 70%;
    }
  }

  &-company {
    font-weight: 600;
    margin-bottom: .25rem;
    margin-top: 1rem;
  }

  &-dates {
    margin-top: 0;
  }
}
